<template>
  <div>
    <b-row cols="1" cols-lg="2" class="no-print">
      <b-col class="p-1">
        <b-form-group
          id="input-group-model-number"
          label="Model Number"
          label-for="input-model-number"
          class="text-left font-weight-bold"
        >
          <b-form-input
            id="input-model-number"
            v-model="modelNumber"
            list="presetModelNumbers"
          ></b-form-input>
          <datalist id="presetModelNumbers">
            <option
              v-for="presetModel in presetModelNumbers"
              :key="presetModel.number"
              :value="presetModel.number"
            >
              {{ presetModel.description }}
            </option>
          </datalist>
        </b-form-group>
      </b-col>

      <b-col class="p-1">
        <b-form-group
          id="input-group-serial-number"
          label="Serial Number"
          label-for="input-serial-number"
          class="text-left font-weight-bold"
        >
          <b-form-input
            id="input-serial-number"
            v-model="serialNumber"
          ></b-form-input>
        </b-form-group>
      </b-col>
    </b-row>

    <b-row cols="1" class="no-print">
      <b-col class="p-1">
        <b-button variant="outline-primary" v-on:click="clearInputs"
          >Reset</b-button
        >
      </b-col>
    </b-row>

    <b-row cols="1" class="mt-5">
      <b-col class="p-1">
        <div v-if="modelNumberComputed && serialNumberComputed">
          <barcode
            v-bind:value="modelNumberComputed"
            format="CODE128"
            font="Fira Code"
          ></barcode>
          <barcode
            v-bind:value="serialNumberComputed"
            format="CODE128"
            font="Fira Code"
          ></barcode>
        </div>

        <div
          v-if="!modelNumber || !serialNumber"
          class="alert alert-warning"
          role="alert"
        >
          Enter or choose a preset Model and Serial Number
        </div>
      </b-col>
    </b-row>
  </div>
</template>

<script>
class PresetModel {
  constructor(number, description) {
    this.number = number
    this.description = description
  }
}

const MODEL_NUMBER_PARAM_KEY = "mn"
const SERIAL_NUMBER_PARAM_KEY = "sn"
const DEFAULT_MODEL_NUMBER = ""
const DEFAULT_SERIAL_NUMBER = "999999001"
const PRESET_MODEL_NUMBERS = [
  new PresetModel("NDV038G101CTL0BA", "5 Series Water to Air"),
  new PresetModel("NVV0361101CTL0JN", "7 Series Water to Air"),
  new PresetModel("NHW084H180ABSSA", "5 Series High Temp Water-to-Water"),
  new PresetModel("NDS049110ACNN0AN", "5 Series Outdoor Split"),
  new PresetModel("NVZ033A110CNN0KN", "7 Series Indoor Split"),
  new PresetModel("NDZ049A11AC", "*Unsupported* Envision Indoor Split"),
  new PresetModel(
    "SVH050A152CR1S11",
    "*Unsupported* 7 Series Split Air Handler"
  ),
]

export default {
  name: "UnitBarcode",
  data() {
    return {
      modelNumber: DEFAULT_MODEL_NUMBER,
      serialNumber: DEFAULT_SERIAL_NUMBER,
      presetModelNumbers: PRESET_MODEL_NUMBERS,
    }
  },
  computed: {
    modelNumberComputed: function () {
      return this.modelNumber.toUpperCase()
    },
    serialNumberComputed: function () {
      return this.serialNumber.toUpperCase()
    },
  },
  watch: {
    modelNumberComputed: function (newValue) {
      let newQuery = {}
      if (newValue) {
        newQuery = { mn: newValue, sn: this.serialNumberComputed }
      }
      this.$router.replace({
        query: newQuery,
      })
    },
    serialNumberComputed: function (newValue) {
      let newQuery = {}
      if (this.modelNumberComputed) {
        newQuery = { mn: this.modelNumberComputed, sn: newValue }
      }
      this.$router.replace({
        query: newQuery,
      })
    },
  },
  methods: {
    clearInputs: function () {
      this.modelNumber = DEFAULT_MODEL_NUMBER
      this.serialNumber = DEFAULT_SERIAL_NUMBER
    },
  },
  created() {
    const queryModelNumber = this.$route.query[MODEL_NUMBER_PARAM_KEY]
    if (queryModelNumber) {
      this.modelNumber = queryModelNumber
    }
    const querySerialNumber = this.$route.query[SERIAL_NUMBER_PARAM_KEY]
    if (querySerialNumber) {
      this.serialNumber = querySerialNumber
    }
  },
}
</script>
